import React, { useEffect, useState,useRef } from 'react'
import app, { auth } from '../../firebase'
import { MenuItem } from './MenuItem'
import { MenuItem2 } from './MenuItem2'
import './Navbar.css'
import { CSSTransition } from 'react-transition-group';
import { Link, useNavigate } from 'react-router-dom'


function Navbar2(props) {
const [logged, setLogged] = useState(false)
const [name,setName] = useState("")
const [url,setUrl] = useState("")
const [open, setOpen] = useState(false);

    useEffect(() => {
        app.auth().onAuthStateChanged(user => {
            if (user) {
setLogged(true)
app.database().ref().child('users/'+user.uid+'/username').on('value', snapshot => {
            
    setName(snapshot.val()) 
     
})
app.database().ref().child('users/'+user.uid+'/profile/url/').on('value', snapshot => {
   
   setUrl(snapshot.val()) 
    
})
            }
            else
            {
                setLogged(false)
            }
        })
    }, [])
    return (
        <nav className = "NavbarItems">
              <b className="navbar-logo" >{props.title}</b>
              <ul className= 'nav-menu'>
              
               {
                   
            MenuItem2.map((item,index)=>{
                   return(
                    <li key ={index} >
                    <a className={item.cName} href={item.url}>
                    {item.title}
                    </a> 
                    </li>
                   )
               })
               }
              
               </ul>
               <div >
             <div style={{display:"block"}} onClick={() => setOpen(!open)}>
               <img src={url ||"https://img.icons8.com/ios-glyphs/30/000000/user-male-circle.png"} onClick={() => setOpen(!open)} style={{
                 color:"white",borderRadius:"50%", display:"block",width:"30px",height:"30px",lineHeight:"30px"}}/>
                 <b style={{fontSize:"15px"}}>{name}</b>  
             </div>
               

{open && <DropdownMenu></DropdownMenu>}
               </div>
              </nav>
    )
}
function DropdownMenu() {
    const [activeMenu, setActiveMenu] = useState('main');
    const [menuHeight, setMenuHeight] = useState(null);
    const dropdownRef = useRef(null);
    const [logged, setLogged] = useState(false)
    const history = useNavigate()
    const [type, setType] = useState('')

    useEffect(() => {
        app.auth().onAuthStateChanged(user => {
            if (user) {
setLogged(true)
app.database().ref().child('users/'+user.uid+"/type").on('value', snapshot => {
    if (snapshot.exists()) {
      setType(snapshot.val())
     

    }
    else{
      setType("")
    }
  })
            }
            else
            {
                setLogged(false)
            } 
        })
        setMenuHeight(dropdownRef.current?.firstChild.offsetHeight)
    }, [])
  
   
  
    function calcHeight(el) {
      const height = el.offsetHeight;
      setMenuHeight(height);
    }
  
    function DropdownItem(props) {
      return (
        <a href="#" className="menu-item" onClick={() => props.goToMenu && setActiveMenu(props.goToMenu)}>
          <span className="icon-button">{props.leftIcon}</span>
          {props.children}
          <span className="icon-right">{props.rightIcon}</span>
        </a>
      );
    }
  
    return (
      <div className="dropdown" style={{ height: menuHeight }} ref={dropdownRef}>
  
        <CSSTransition
          in={activeMenu === 'main'}
          timeout={500}
          classNames="menu-primary"
          unmountOnExit
          onEnter={calcHeight}>
          <div className="menu">
            <DropdownItem><Link to="/profile" style={{ textDecoration: "none"}}><i class="fas fa-user-circle"></i>My Profile</Link></DropdownItem>
            <DropdownItem goToMenu="inboxes" >
            <i class="fas fa-inbox"></i>Inboxes
            </DropdownItem>
            <DropdownItem
              goToMenu="settings">
              <i class="fas fa-cog"></i>Settings
            </DropdownItem>
            <DropdownItem>
            { logged ? 
             <b onClick={()=>{auth.signOut() 
        }} style={{ textDecoration: "none"}}><i class="fas fa-sign-in-alt"></i>Log out</b>
        : 
        <Link to="/login" style={{ textDecoration: "none"}}><i class="fas fa-sign-in-alt"></i>Login</Link>
        }
        </DropdownItem>
        
  
          </div>
        </CSSTransition>
  
        <CSSTransition
          in={activeMenu === 'settings'}
          timeout={500}
          classNames="menu-secondary"
          unmountOnExit
          onEnter={calcHeight}>
          <div className="menu">
            <DropdownItem goToMenu="main" >
              <h2><i class="fas fa-long-arrow-alt-left"></i>Settings</h2>
            </DropdownItem>
            <DropdownItem ><Link to="/settings" style={{fontSize:"15px"}}><i class="fas fa-user-cog"></i>Account Settings</Link></DropdownItem>
            <DropdownItem ><Link to="/noti" style={{fontSize:"15px"}}><i class="fas fa-bell"></i>Notifications</Link></DropdownItem>
            {
                type=="photo" ?
                <DropdownItem ><Link to="/dash_p" style={{fontSize:"15px"}}><i class="fas fa-bell"></i>More</Link></DropdownItem>
                :
                <DropdownItem ><Link to="/dash_c" style={{fontSize:"15px"}}><i class="fas fa-cog"></i>More</Link></DropdownItem>
            }
            
          </div>
        </CSSTransition>
  
        <CSSTransition
          in={activeMenu === 'inboxes'}
          timeout={500}
          classNames="menu-secondary"
          unmountOnExit
          onEnter={calcHeight}>
          <div className="menu">
            <DropdownItem goToMenu="main" >
              <h2><i class="fas fa-long-arrow-alt-left"></i>Inboxes</h2>
            </DropdownItem>
            <DropdownItem>Unread Inboxes</DropdownItem>
            <DropdownItem >Read Inboxes</DropdownItem>
          </div>
        </CSSTransition>
      </div>
    );
  }

export default Navbar2
